import React from 'react';
import DefaultLayout from '../components/layouts/DefaultLayout';

const Dashboard = React.lazy(() => import('../views/factory/Dashboard'));
const Shop = React.lazy(() => import('../views/shop/Shop'));
const Factory = React.lazy(() => import('../components/container/Factory'));
const Phase1 = React.lazy(() => import('../components/container/Phase1'));
const Phase2 = React.lazy(() => import('../components/container/Phase2'));

// https://github.com/ReactTraining/react-router/tree/master/packages/react-router-config
const routes = [
  { path: '/', exact: true, name: 'Home', component: DefaultLayout },
  { path: '/dashboard', name: 'Dashboard', component: Dashboard },
  { path: '/shop/:id',exact: true, name: 'Shop', component: Shop },
  { path: '/factory', name: 'Factory', component: Factory },
  { path: '/phase1', name: 'Phase1', component: Phase1 },
  { path: '/phase2', name: 'Phase2', component: Phase2 },
];

export default routes;
